import React from "react";
import Image from "next/image";

interface SidebarItemProps {
  icon: string;
  label: string;
  isActive?: boolean;
}

const SidebarItem: React.FC<SidebarItemProps> = ({ icon, label, isActive }) => {
  return (
    <div
      className={`flex items-center gap-4 p-2 rounded cursor-pointer transition-colors ${
        isActive
          ? "text-primary bg-gray-200"
          : "text-secondary hover:bg-gray-100"
      }`}
      style={{
        color: isActive ? "var(--color-primary-700)" : "var(--color-neutral-400)",
        backgroundColor: isActive ? "var(--background-2)" : "transparent",
      }}
    >
      <Image src={icon} alt={label} width={24} height={24} />
      <span className="font-medium">{label}</span>
    </div>
  );
};

export default SidebarItem;
